.container {
  margin-top: 40px;
}
.form {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin: 0 auto;
  background-color: #fff;
  padding: 20px 30px;
  border-radius: 10px;
  box-shadow: 0px 2px 21px -4px rgba(0, 0, 0, 0.42);
}
.title {
  align-self: center;
}
.form label {
  display: flex;
  padding-top: 20px;
  padding-bottom: 10px;
  align-items: center;
}
.field {
  padding: 10px;
  border-radius: 5px;
  background-color: rgba(190, 185, 192, 0.25);
}
.btn {
  margin-top: 20px;
  align-self: center;
  padding: 10px;
  background-color: #b38fe8;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  transition: 0.1s ease;
}
.btn:hover {
  background-color: #9872df;
  transition: 0.1s ease;
  box-shadow: unset;
}
.error {
  color: #ff4b23;
  padding-top: 3px;
}
.acceptLicence p {
  margin-left: 10px;
}

